
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Karla', sans-serif;
  overflow: hidden;
  background-color: #5f8fe7;
}


/* navbar styling*/
/* for light mode */
.navbar{
  display: flex;
  justify-content: space-between;
  position: sticky;
  width: 100%;
  top: 0;
  left: 0;
  padding: 12px 30px;
  background-color: #5f8fe7;
  font-size: 20px;
}
/* for dark mode */
.navbarDark{
  display: flex;
  justify-content: space-between;
  position: sticky;
  width: 100%;
  top: 0;
  left: 0;
  padding: 12px 30px;
  background-color: #0c2c84;
  font-size: 20px;
  color: white;
}

/* for the toggle button of dark and light mode */
.toggler {
  display: flex;
  align-items: center;
}

.toggler p {
  font-size: 0.825rem;
  font-weight: 600;
}

.toggler--slider {
  width: 25px;
  height: 13px;
  background-color: #0c2c84;
  border-radius: 2rem;
  margin-inline: 7px;
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #0c2c84;
  box-sizing: content-box;
}

.toggler--sliderDark{
  margin-inline: 7px;
  cursor: pointer;
  border-radius: 2rem;
  background-color: #5f8fe7;
  border: 1px solid #5f8fe7;
  width: 25px;
  height: 13px;

}

.toggler--slider--circle {
  height: 13px;
  width: 13px;
  background-color: white;
  border-radius: 50%;
}
.toggler--slider--circleDark {
  margin-left: auto;
  height: 13px;
  width: 13px;
  background-color: white;
  border-radius: 50%;
}

.toggler--light {
  color: #2B283A;
}

.toggler--dark {
  color: #D5D4D8;
}


.HomePage{
  padding: 20px 30px;
  background-color: #0c2c84;
  height: 100vh;
  color: #ffffff;
}

.HomePageWhite{
  padding: 20px 30px;
  background-color: #5f8fe7;
  color: black;
  height: 100vh;
}

.Question{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: start;
  color: white;
  height: 100%;
  padding: 20px 0px;
  text-align: center;
}

.QuestionWhite{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: start;
  height: 100%;
  padding: 20px 0px;
  text-align: center;
  color: black;

}

.optionInput{
  visibility: hidden;
}

.Label{
  font-family: 'Karla';
  font-size: 18px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 12px;
  width: 500px;
}

.Label:hover{
  cursor: pointer;
  background-color: yellow;
  border-radius: 2px;
  width: 500px;
}

.customOption{
  left: -8px;
  top: 6px;
  cursor: pointer;
  width: 19px;
  height: 19px;
  border: 2px solid #fffc00;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.customOptionBorder{
  border: 2px solid red;;
}

.customOption::after {
  content: '';
  width: 12px;
  height: 12px;
  background: #ff0000;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 51%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.2s;
}
.optionInput:checked + span {
  border: 2px solid #ff0000;
}

.optionInput:checked + span:after {
  opacity: 1;
}

/* submit button */
.HomePageButton{
  margin-top: 29px;
  width: 100px;
  padding: 10px 1px;
  border-radius: 15px;
  border: none;
  background-color: #fffc00;
  color: #0c2c84;
  font-weight: 600;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.HomePageButton:hover {
  background-color: #212121;
  color: #fffc00;
  cursor: pointer;
}


/* page 2 */
.Page2{
  display: flex;
  height: 100vh;
  width: 100%;
  background-color: #0c2c84; 
}

.Page2White{
  display: flex;
  height: 100vh;
  width: 100%;
  background-color: #5f8fe7;
}

table {
  text-align: center;
  border: 2px solid forestgreen;
  width: 1000px;
  height: 470px;
  background-color: white;
}



th {
  border: 1px solid black;
  padding: 3px 5px;
}
th:nth-child(1) {
  background-color: rgb(222, 170, 74);
  color: rgb(95, 62, 2);
}
td {
  text-align: center;
  border: 1px solid black;
  padding: 11px 14px;

}



td:nth-child(1) {
  background-color: pink;
}
td:nth-child(2) {
  background-color: yellow;
}
td:nth-child(3) {
  background-color: lightgreen;
}
td:nth-child(4) {
  background-color: rgb(204, 150, 255);
}
td:nth-child(5) {
  background-color: lightblue;
}

@media (max-width: 768px) {
  body{
    overflow: auto;
  }
  table {
    width: 100%;
    overflow-x: auto;
  }
  .Page2{
    overflow-x: auto;
        justify-content: none;
    align-items: none;
    text-align: none;
  }
  .Page2White{
    overflow-x: auto;
    justify-content: none;
    align-items: none;
    text-align: none;
  }
}